<template>
  <div>
    <LayoutBox one="" two="">
      <stepsCmp :order-list-data="orderListData"></stepsCmp>

      <div class="order_status">
        <h4></h4>
        <p v-if="orderListData.orderStatus === 0"></p>
        <p v-else-if="orderListData.orderStatus === 1"></p>
        <p v-else-if="orderListData.orderStatus === 2"></p>
        <p v-else-if="orderListData.orderStatus === 3"></p>
        <p v-else-if="orderListData.orderStatus === 4"></p>
        <p v-else-if="orderListData.orderStatus === 9"></p>
        <p v-else-if="orderListData.orderStatus === 10"></p>
        <el-button class="express" type="text" @click="onExpress"></el-button>
      </div>

      <div class="buyer_infor">
        <div class="buyer_infor_bottom"><h4></h4></div>
        <div class="buyer_infor_box">
          <span><span>{{ orderListData.userName }}</span></span>
          <!-- 0 1 -->
          <span>
            
            <span>{{ orderListData.payType === 2 ? '' : orderListData.payType === 1 ? '' : '' }}</span>
          </span>
          <span>
            
            <span>{{ orderListData.receiverName }}<span>{{ orderListData.receiverPhone }}</span></span>
          </span>
          <span class="address">{{ orderListData.receiverAddress }}</span>
        </div>
      </div>

      <div class="order_details">
        <div class="order_details_bottom"><h4></h4></div>
        <div class="text_box">
          <span><span>{{ orderListData.orderCode }}</span></span>
          <span><span>{{ orderListData.createTime }}</span></span>
          <span><span>{{ orderListData.payTime }}</span></span>
        </div>
      </div>
      <el-table class="goods_table" :data="orderData">
        <el-table-column label="" width="250" align="center">
          <template slot-scope="scope">
            <div class="goods_table_goods">
              <el-image :src="scope.row.prodUrl" fit="cover" style="width: 70px; height: 70px"></el-image>
              <span class="goods_name">{{ scope.row.prodName }}</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="itemSpec" label="" width="120" align="center"></el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 0" style="color: #40ccae"></span>
            <span v-else-if="scope.row.status === 1" style="color: #e39025"></span>
            <span v-else-if="scope.row.status === 2" style="color: #49ca26"></span>
            <span v-else-if="scope.row.status === 3" style="color: #c5fd68"></span>
            <span v-else-if="scope.row.status === 4" style="color: #e02929"></span>
            <span v-else-if="scope.row.status === 10" style="color: #666666"></span>
            <span v-else-if="scope.row.status === 9" style="color: #999999"></span>
            <span v-else-if="orderListData.orderStatus === 0" style="color: #40ccae"></span>
            <span v-else-if="orderListData.orderStatus === 1" style="color: #e39025"></span>
            <span v-else-if="orderListData.orderStatus === 2" style="color: #49ca26"></span>
            <span v-else-if="orderListData.orderStatus === 3" style="color: #c5fd68"></span>
            <span v-else-if="orderListData.orderStatus === 4" style="color: #e02929"></span>
            <span v-else-if="orderListData.orderStatus === 10" style="color: #666666"></span>
            <span v-else-if="orderListData.orderStatus === 9" style="color: #999999"></span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.price }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="count" label="" align="center"></el-table-column>
        <el-table-column prop="discountPrice" label="" align="center">
          <template>
            <span>{{ orderListData.totalDiscountPrice }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template>
            <span>{{ orderListData.totalPrice }}</span>
          </template>
        </el-table-column>
      </el-table>

      <div class="pay_money">
        <div>
          <!--  0- 1- 2- 3-/ 4- 5- 9- 10- 0-10- -->
          <el-button
            v-if="orderListData.orderStatus == 0"
            class="inquire"
            style="width: 100px"
            type="success"
            size="small"
            round
          ></el-button>
          <el-button
            v-if="orderListData.orderStatus == 0"
            style="width: 100px"
            size="small"
            round
            @click="closeOrder"
          ></el-button>
          <el-button
            v-if="orderListData.orderStatus == 1"
            class="inquire"
            style="width: 100px"
            type="success"
            size="small"
            round
            @click="onShip"
          ></el-button>
          <el-button
            v-if="orderListData.orderStatus == 2"
            class="inquire"
            style="width: 100px"
            type="success"
            size="small"
            round
            @click="onLodop"
          ></el-button>
          <el-button
            v-if="orderListData.orderStatus == 1"
            style="width: 100px"
            size="small"
            round
            @click="onRequestRefund"
          ></el-button>
          <el-button
            v-if="orderListData.orderStatus == 2"
            class="inquire"
            style="width: 100px"
            type="success"
            size="small"
            round
            @click="onOrderRecOvertime"
          ></el-button>
        </div>
        <div>
          <span></span>
          <span class="money">{{ orderListData.payAmount }}</span>
          <span></span>
        </div>
      </div>
    </LayoutBox>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form ref="form" :model="form" :rules="rules">
        <el-form-item label="" label-width="120px" prop="price">
          <el-input v-model="form.price" class="price_input"></el-input>
          <span class="price_remarks">{{ orderListData.payAmount }}</span>
        </el-form-item>
        <el-form-item label="" label-width="120px" prop="returnReason">
          <el-input v-model="form.returnReason" class="return_reason_input"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm('form')"> </el-button>
      </div>
    </el-dialog>

    <lodopCmp ref="lodop" />
  </div>
</template>

<script>
import stepsCmp from '@/views/order/order_content/steps_cmp/index.vue'
import LayoutBox from '@/components/LayoutBox/index'
import { MIXINS_FORMRULE } from '@/mixins/form.js'
import { MIXINS_ORDERHEADFINDBYID_API } from '@/mixins/api.js'
import lodopCmp from '@/components/lodop_cmp/index.vue'

export default {
  name: 'OrderContent',

  components: {
    stepsCmp, LayoutBox, lodopCmp,
  },

  data() {
    return {
      pageNo: 1, // 1
      orderHeaderId: '', // id
      orderData: [], // 
      orderListData: {}, // 
      dialogFormVisible: false, // 
      form: {}, // 
      rules: {
        price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        returnReason: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },

  mixins: [
    MIXINS_ORDERHEADFINDBYID_API,
    MIXINS_FORMRULE
  ],

  created () {
    this.orderHeaderId = this.$route.query.id
  },

  mounted () {
    this._findOrderList()
    this._getOrderData()
  },

  methods: {
    // 
    closeOrder () {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this._orderClose()
      })
    },
    // 
    onShip () {
      this.$router.push({ path: '/orderList/orderContent/ship', query: { id: this.orderListData.id }})
    },
    // 
    onOrderRecOvertime () {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.apiOrderRecOvertime()
      })
    },
    // 
    onRequestRefund () {
      this.dialogFormVisible = true
    },
    // 
    submitForm (formName) {
      let data = this.ruleForm(formName)
      this.apiOrderRefund(data)
    },
    // 
    onExpress () {
      let logisticsData = {
        logisticsStatus: this.orderListData.postType,
        wbNo: this.orderListData.logistcsNumber,
        expressCompanyName: this.orderListData.logisticsCompany,
        receiverName: this.orderListData.receiverName,
        receiverPhone: this.orderListData.receiverPhone,
        receiverAddress: this.orderListData.receiverAddress
      }
      this.$router.push({
        path: '/orderList/orderContent/expressDetail',
        query: { logisticsData: JSON.stringify(logisticsData) }
      })
    },
    // 
    onLodop () {
      this.orderHeadFindById(this.$route.query.id).then((res) => {
        console.log('', res)
        if (res.data.success) {
          this.$refs.lodop.printCode(res.data.data)
          this.$router.push({ path: '/orderList/orderContent', query: { id: this.$route.query.id }})
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },

    // api
    // 
    _getOrderData () {
      const DATA = {
        pageNo: this.pageNo,
        orderHeaderId: this.orderHeaderId
      }
      this.$http.post('/woho/order/item/query', DATA)
        .then((res) => {
          this.orderData = res.data.data.list
        })
    },
    // id
    _findOrderList () {
      this.$http.post('/woho/order/head/findById/' + this.orderHeaderId)
        .then((res) => {
          this.orderListData = res.data.data
          console.log(res.data.data)
        })
    },
    // 
    _orderClose () {
      this.$http.post('/woho/order/head/orderClose/' + this.orderHeaderId)
        .then((res) => {
          console.log(res.data.data)
          if (res.data.success) {
            this.$message.success('!')
          }
        })
    },
    // 
    apiOrderRecOvertime () {
      let data = {
        id: this.orderHeaderId
      }
      this.$http.post('/woho/order/head/orderRecOvertime', data)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 
    apiOrderRefund (formData) {
      let data = {
        type: 0,
        id: this.orderHeaderId,
        ...formData
      }
      this.$http.post('/woho/order/head/refundAll', data)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('')
            this.$router.push('/orderList')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    }
  }
}
</script>

<style lang="scss" scoped>
  $themColor: #5bd9be;

  .order_status{
    width: 90%;
    background-color: #e6fffc;
    border-radius: 5px;
    border: solid 1px $themColor;
    margin: 56px auto auto auto;
    padding-left: 25px;
    .express{
      color: $themColor;
      display: inline-block;
      margin: 15px 0px;
    }
  }
  .buyer_infor{
    width: 90%;
    margin: 25px auto auto auto;
    .buyer_infor_bottom{
      border-bottom: 1px solid #bfbfbf;
    }
    .buyer_infor_box{
      margin-top: 15px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      .address{
        margin-left: 70px;
      }
    }
  }
  .order_details{
    width: 90%;
    margin: 66px auto auto auto;
    .order_details_bottom{
      border-bottom: 1px solid #bfbfbf;
    }
    .text_box{
      margin-top: 15px;
      font-size: 14px;
      display: flex;
      justify-content: space-between;
      width: 70%;
    }
  }
  .goods_table{
      width: 90%;
      margin: 15px auto;
    .goods_table_goods{
      display: flex;
      align-items: center;
      .goods_name{
        width: 150px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
        text-align: left;
        margin: 5px;
      }
    }
  }
  .pay_money{
    font-size: 14px;
    font-weight: bold;
    width: 90%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    .money{
      color: #f9930f;
    }
  }

  .price_input{
    width: 200px
  }
  .price_remarks{
    color: #999999;
    font-size: 7px;
    margin-left: 15px;
  }
  .return_reason_input{
    width: 200px
  }
</style>
